import React from 'react';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import ReactDOM from 'react-dom';
import {
  BrowserRouter as Router,
  Route,
  Routes
} from 'react-router-dom';

ReactDOM.render(
  <Router>
    <Routes>
      <Route path="/wordle-fork/beebzdle" element={
        <App
          gameName="Beebzdle"
          customDictionary={["larby", "beebz", "poopy", "ricks"]}
          solution="RICKS"
        />
      } />
      <Route path="/wordle-fork/discordle" element={
        <App
          gameName="Discordle"
          customDictionary={["weebs", "furry", "degen"]}
          solution="DEGEN"
        />
      } />
      <Route path="/wordle-fork/kimatraidle" element={
        <App
          gameName="Kimatraidle"
          customDictionary={["anuja", "kiran", "aysha", "sindh"]}
          solution="SINDH"
        />
      } />
      <Route path="/wordle-fork/thawardle" element={
        <App
          gameName="Thawardle"
          customDictionary={["hathi", "kirti", "radha", "kiran", "nanis", "dadis"]}
          solution="HATHI"
        />
      } />
    </Routes>
  </Router>,

  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
