import Key from '../key/Key';
import './Keyboard.css';

function Keyboard(props) {
  return (
    <div className="Keyboard">
      <div className="Line">
        {["Q", "W", "E", "R", "T", "Y", "U", "I", "O", "P"].map((letter) => {
          return (
            <Key
              backgroundColor={getBackgroundColor(letter)}
              letter={letter}
              key={letter}
              handleClick={() => props.onKeyboardKeyPress(letter)} />)
        })}
      </div>
      <div className="Line">
        {["A", "S", "D", "F", "G", "H", "J", "K", "L"].map((letter) => {
          return (
            <Key
              backgroundColor={getBackgroundColor(letter)}
              letter={letter}
              key={letter}
              handleClick={() => props.onKeyboardKeyPress(letter)} />)
        })}
      </div>
      <div className="Line">
        {["", "Z", "X", "C", "V", "B", "N", "M", ""].map((letter) => {
          return (
            <Key
              backgroundColor={getBackgroundColor(letter)}
              letter={letter}
              key={letter}
              handleClick={() => props.onKeyboardKeyPress(letter)} />)
        })}
      </div>
    </div>
  );

  function getBackgroundColor(letter) {
    if (props.inPlace.includes(letter)) {
      return "#91ed8e"
    }
    else if (props.inWord.includes(letter)) {
      return 'yellow'
    }
    else if (props.disqualified.includes(letter)) {
      return 'grey'
    }
    else {
      return 'white'
    }
  }
}

export default Keyboard;
