import './Block.css';

function Block(props) {
  return (
    <div className="Block"
      style={{
        backgroundColor: getBackgroundColor(props.state),
      }}>
      <p>{props.letter.toUpperCase()}</p>
    </div>
  );

  function getBackgroundColor(state) {
    switch (state) {
      case "IN_WORD": return "yellow"
      case "IN_PLACE": return "#91ed8e"
      case "WRONG": return "grey"
      default: return "#ffffff"
    }
  }
}

export default Block;
