import Block from '../block/Block';
import './Game.css';

function Game(props) {
  return (
    props.game.map((line) => {
      return (<div className="Game"> 
        {
          [0, 1, 2, 3, 4].map((number) => {
            return (<Block key={number}
              letter={line[number] !== undefined ? line[number].letter : ''} 
              state={line[number] !== undefined ? line[number].state : ''}/>)
          })
        }
      </div>)
    })
  );
}

export default Game;
