import './App.css';
import Game from './game/Game';
import Keyboard from './keyboard/Keyboard';
import 'react-simple-keyboard/build/css/index.css';
import { useState } from 'react';
import React from 'react';
import words from 'an-array-of-english-words';

function App(props) {
  words = words.concat(props.customDictionary);
  const word = props.solution.split('');

  const [lines, setLines] = useState([[]]);
  const [gameState, setGameState] = useState("IN_PROGRESS");
  const [showInvalidWordModal, setShowInvalidWordModal] = React.useState(false)
  const [showSuccessModal, setShowSuccessModal] = React.useState(false)
  const [disqualified, setDisqualified] = React.useState([])
  const [inPlace, setInPlace] = React.useState([])
  const [inWord, setInWord] = React.useState([])

  function closeInvalidWordModal() {
    setTimeout(function () {
      setShowInvalidWordModal(false)
    }, 3000);
  }

  return (
    <div className="App" style={{
      backgroundColor: getBackgroundColor(gameState),
    }}>
      <div className="Header">{props.gameName}</div>
      <Game game={lines} />
      <Keyboard onKeyboardKeyPress={(letter) => updateLines(letter)} disqualified={disqualified} inWord={inWord} inPlace={inPlace} />
      {showInvalidWordModal && <div className="InvalidWordModal">Invalid Word</div>}
      {showSuccessModal &&
        <div className="SuccessModal">
            You Did It!  
          <br />
          <div class="ResultHolder">
            {generateEmojiText()}
          </div>
          <button onClick={() => {navigator.clipboard.writeText(generateEmojiArray().join('\n'))}}>Copy To Clipboard</button>
        </div>}

    </div>
  );

  function getBackgroundColor(gameState) {
    switch (gameState) {
      case "WIN": return 'green'
      case "LOSS": return 'red'
      default: return 'white'
    }
  }

  function updateLines(newLetter) {
    if (gameState === "WIN" || gameState === "LOSS") return;
    if (lines[lines.length - 1].length === 5 && newLetter !== '' && newLetter !== '') {
      return;
    }
    else if (lines[lines.length - 1].length === 5 && newLetter === '') {
      if (!words.includes(lines[lines.length - 1].map((word) => word.letter).join('').toLowerCase())) {
        setShowInvalidWordModal(true);
        closeInvalidWordModal()
      }
      else {
        checkPuzzle()
        if (lines[lines.length - 1].filter((letter) => letter.state === "IN_PLACE").length === 5) {
          setGameState("WIN");
          setShowSuccessModal(true);
        }
        else if (lines.length === 6) {
          setGameState("LOSS");
        }
        else {
          lines.push([]);
        }
      }
    }
    else if (newLetter === '') {
      return;
    }
    else if (newLetter === '') {
      if (lines[lines.length - 1].length > 0) {
        lines[lines.length - 1].pop();
      }
    }
    else {
      lines[lines.length - 1].push({ letter: newLetter });
    }
    setLines([...lines]);
  }
  
  function checkPuzzle() {
    const line = lines[lines.length - 1];
    for (let i = 0; i < 5; i++) {
      if (line[i].letter === word[i]) {
        line[i].state = "IN_PLACE"
        setInPlace(inPlace => [...inPlace, line[i].letter]);
      }
      if (!word.includes(line[i].letter)) {
        line[i].state = "WRONG"
        setDisqualified(disqualified => [...disqualified, line[i].letter])
      }
    }
    for (let i = 0; i < 5; i++) {
      if (word.includes(line[i].letter)) {
        let wordIndicies = new Set(word.reduce(function (a, e, j) {
          if (e === line[i].letter)
            a.push(j);
          return a;
        }, []));

        let userIndicies = new Set(line.reduce(function (a, e, j) {
          if (e.letter === line[i].letter)
            a.push(j);
          return a;
        }, [])); 

        for (const index of userIndicies.values()) {
          if (wordIndicies.has(index)) {
            wordIndicies.delete(index);
            userIndicies.delete(index);
          }
        }
        for (const index of userIndicies.values()) {
          if (wordIndicies.size === 0) {
            line[index].state = "WRONG"
          } else {
            line[index].state = "IN_WORD"
            setInWord(inWord => [...inWord, line[index].letter])
          }
        }
      }
    }

    let linesCopy = lines;
    linesCopy[linesCopy.length - 1] = line
    setLines([...linesCopy])
  }

  function generateEmojiText() {
    let x = generateEmojiArray()
    return x.map((line) => {
      return (<div class="ResultLine"><p>{line}</p></div>)
    })
  }

  function generateEmojiArray() {
    const arr = [`${props.gameName} ${lines.length}/5`]
    const emojis = lines.map((line) => {
      return line.map((letter) => {
        switch (letter.state) {
          case "IN_PLACE": return ""
          case "IN_WORD": return ""
          default: return ""
        }
      }).join('')
    })
    return arr.concat(emojis);
  }
}

export default App;